import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Homepage from "./routes/Homepage";
import Stream from "./routes/Stream";
import Channel from "./routes/Channel";
import Favorites from "./routes/Favorites";
import Multiview from "./routes/Multiview";
import Setting from "./routes/Setting";
import About from "./routes/About";
import LoginPage from "./routes/Login";
import AccountSetting from "./routes/AccountSetting";
import { useRecoilValue } from "recoil";
import { isLoggedInAtom } from "./atoms";

function Routers({ userObj }) {
  const isLoggedIn = useRecoilValue(isLoggedInAtom);
  return (
    <Router>
      <Routes>
        <Route path={process.env.PUBLIC_URL + "/"} element={<Homepage />} />
        <Route path={process.env.PUBLIC_URL + "/video/:yt_video"} element={<Stream />} />
        <Route path={process.env.PUBLIC_URL + "/channel/:yt_channel_id"} element={<Channel />} />
        <Route path={process.env.PUBLIC_URL + "/multiview"} element={<Multiview />} />
        <Route path={process.env.PUBLIC_URL + "/setting" }element={<Setting userObj={userObj} />} />
        <Route path={process.env.PUBLIC_URL + "/about"} element={<About userObj={userObj} />} />
        <Route path={process.env.PUBLIC_URL + "/login"} element={<LoginPage />} />
        <Route path={process.env.PUBLIC_URL + "/accountsetting" } element={<AccountSetting />} />
        {isLoggedIn ? (
          <>
            <Route path={process.env.PUBLIC_URL + "/favorites"} element={<Favorites />} />
          </>
        ) : (
          <>
            <Route path={process.env.PUBLIC_URL + "/favorites"} element={<LoginPage />} />
          </>
        )}
      </Routes>
    </Router>
  );
}
export default Routers;
